import React, { Component } from 'react';

class ProjectItem extends Component {

  deleteProject(id){
    console.log('test')
    this.props.onDelete(id)
  }
  render() {

    return (
      <li className="Beers">
        <strong>{this.props.project.title}</strong> {this.props.project.rating} < a href="#x" onClick={this.deleteProject.bind(this, this.props.project.id)}>X</a>
        <div className="info"> {this.props.project.Brewery}</div>
        <div className="info"> {this.props.project.style}</div>
      </li>
    );
  }
}

export default ProjectItem;



